"use client";
import React, { useState } from "react";
import { Form, Button, Row, Col, Container } from "react-bootstrap";
import { useRouter } from "next/navigation";

export default function AddUserPage() {
  const router = useRouter();
  const [status, setStatus] = useState("user");

  const handleStatusChange = (e) => {
    const value = e.target.value;
    setStatus(value);
    if (value === "admin") {
      router.push("/add-user/admin");
    }
  };

  return (
    <Container fluid className="vh-100 d-flex justify-content-center align-items-center bg-dark">
      <div className="p-5 bg-light w-75 rounded-3 d-flex justify-content-between align-items-center">
        <div style={{ width: "10%", height: "80%", backgroundColor: "#34AFA1", borderRadius: 10 }} />
        <div style={{ width: "80%" }}>
          <h3 className="text-center mb-4">Add user</h3>
          <Form>
            <Row>
              <Col>
                <Form.Label>Name</Form.Label>
                <Form.Control type="text" value="Mariya" />
              </Col>
              <Col>
                <Form.Label>Surname</Form.Label>
                <Form.Control type="text" value="Zajceva" />
              </Col>
              <Col xs="auto" className="d-flex align-items-end">
                <div
                  style={{
                    width: 64,
                    height: 64,
                    borderRadius: "50%",
                    backgroundColor: "#ddd",
                    marginLeft: 20,
                  }}
                />
              </Col>
            </Row>

            <Row className="mt-3">
              <Col>
                <Form.Label>Email</Form.Label>
                <Form.Control type="email" value="papirosa@otsos.by" />
              </Col>
              <Col>
                <Form.Label>Phone number</Form.Label>
                <Form.Control type="text" value="+34 1111111" />
              </Col>
            </Row>

            <Row className="mt-3">
              <Col>
                <Form.Label>ID</Form.Label>
                <Form.Control type="text" value="153264" />
              </Col>
              <Col>
                <Form.Label>Address</Form.Label>
                <Form.Control type="text" value="Av. de Madrid" />
              </Col>
              <Col>
                <Form.Label>City</Form.Label>
                <Form.Control type="text" value="Jaen" />
              </Col>
            </Row>

            <Row className="mt-3">
              <Col>
                <Form.Label>Sub type</Form.Label>
                <Form.Select defaultValue="VIP">
                  <option>Standard</option>
                  <option>Premium</option>
                  <option>VIP</option>
                </Form.Select>
              </Col>
              <Col>
                <Form.Label>Status</Form.Label>
                <Form.Select value={status} onChange={handleStatusChange}>
                  <option value="user">user</option>
                  <option value="admin">admin</option>
                </Form.Select>
              </Col>
            </Row>
             <div className="w-16 h-16 rounded-full bg-gray-300 mb-6 overflow-hidden border border-gray-400">
              <Image
                src="/images/user.jpg"
                alt="Profile Avatar"
                width={64}
                height={64}
                className="object-cover w-full h-full"
              />
            </div> 

            <div className="text-center mt-4">
              <Button variant="success" className="px-5">
                Create
              </Button>
            </div>
          </Form>
        </div>
        <div style={{ width: "10%", height: "80%", backgroundColor: "#34AFA1", borderRadius: 10 }} />
      </div>
    </Container>
  );
}

